.footer{
    background: #FEF8F6;
    .footer_end{
        text-align: center;
        p{
            font-weight: 500;
            font-size: $s_16;
            color: #1A0A03;
            opacity: 0.7; 
            padding-top: 1.765vw;
            padding-bottom: 1.765vw;
        }
    }
    .footer__wrapper{
        display: flex;
        justify-content: space-between;
        padding-top: 5.882vw;
        padding-bottom: 2.353vw;
        border-bottom: 1px solid #a6a4a43e;
        @media (max-width: 1100px){
            flex-wrap: wrap;
        }
        @media (max-width: 600px){
            justify-content: start;
        }
        .footer__first{
            .footer__top{
                font-weight: 400;
                font-size: $s_18;
                line-height: 160%;
                color: #1A0A03;
                opacity: 0.7;
                padding-top: 1.353vw;
                padding-bottom: 1.176vw;
            }
            .footer__bot{
                font-weight: 400;
                font-size: $s_18;
                color: #1A0A03;
                opacity: 0.7;
            }
            .footer__mid{
                padding-top: 0.588vw;
                img{
                    padding-right: 1.471vw;
                    @media (min-width: 900px){
                        &:hover{
                            transform: scale(1.1);
                            transition: all .5s ease-in-out;
                        }
                    }
                }
            }
        }
        .footer__insider{
            @media (max-width: 600px){
               padding-right: 40px;
            }
            li{
                padding-bottom: 0.588vw;
                font-weight: 400;
                font-size: $s_18;
                color: $black;
                opacity: 0.7;
                &:first-child{
                    padding-bottom: 1.824vw;
                    font-weight: 400;
                    font-size: $s_18;
                    color: $black;
                    opacity: 1;
                }
            }
        }
        .footer__last{
            .footer__last__top{
                font-weight: 400;
                font-size:  $s_18;
                color: #1A0A03;
            }
            .footer__last__bot{
                font-weight: 400;
                font-size:  $s_18;
                color: #1A0A03;
                opacity: 0.7;
                padding-top: 1.824vw;
                padding-bottom: 2.235vw;
            }
            li{
                .footer__input{
                    width: 100%;
                    max-width: 224px;
                    height: 52px;
                    border: 1px solid rgba(0, 0, 0, 0.1);
                    border-radius: 5px;
                    padding-left: 20px;
                    color: #1a0a037d;
                    &::placeholder{
                        font-weight: 400;
                        font-size: $s_14;
                        color: #1A0A03;
                        opacity: 0.6;
                    }
                    @media (max-width: 600px){
                        max-width: 200px;
                     }
                }
                .footer__btn{
                    width: 100%;
                    max-width: 96px;
                    height: 52px;
                    background: $primary;
                    color: $white;
                    border: 0;
                    margin-left: -8px;
                    &:hover{
                        @media (min-width: 900px) {
                            color: $black;
                            background: #FDEFE9;
                            border-radius: 10%;
                            transform: scale(1.1);
                            transition: all .5s ease-in-out;
                            box-shadow: 5px 0 5px 5px $white;
                        }
                    }
                }
            }
        }
    }
}
