.puma{
    padding-top: 8.235vw;
    .puma__wrapper{
        display: flex;
        @media (max-width: 950px){
            flex-wrap: wrap;
            flex-direction: column-reverse;
            align-items: center;
            max-width: 488px;
            margin: 0 auto;
        }
        img{
            width: 100%;
            max-width: 488px;
            @media (max-width: 955px){
                max-width: 400px;
            }
            @media (max-width: 955px){
                max-width: 488px;
            }
        }
        .puma__box{
            width: 100%;
            height: 334px;
            max-width: 652px;
            background: #81a8da26;
            border-radius: 0px 10px 10px 0px;
            padding-left: 3.706vw;
            padding-top: 3.706vw;
            @media (max-width: 925px){
                height: 280px;
            }
            h3{
                font-family: 'Source Serif Pro';
                font-weight: 600;
                font-size: $s_31;
                color: $black;
            }
            p{
                font-weight: 400;
                font-size: $s_18;
                color: #1A0A03;
                opacity: 0.7;
                padding-top: 1.176vw;
                padding-bottom: 2.353vw;
                @media (max-width: 1105px){
                    padding-right: 20px;
                    br{
                        display: none;
                        padding-right: 20px;
                    }
                }
                @media (max-width: 800px){
                    br{
                        display: block;
                        padding-right: 0;
                    }
                    padding-bottom: 7vw;
                }
            }
            .puma__input{
                width: 100%;
                max-width: 24.059vw;
                height: 51px;
                padding-left: 20px;
                border: 1px solid rgba(0, 0, 0, 0.2);
                border-radius: 5px;
                color: #1a0a037d;
                &::placeholder{
                    color: $black;
                    opacity: 0.5;
                }
                @media (max-width: 955px){
                    max-width: 28.059vw;
                }
                @media (max-width: 864px){
                    max-width: 250px;
                }
                @media (max-width: 422px){
                    max-width: 210px;
                    padding-left: 10px;
                }
            }
            .btn{
                width: 100%;
                max-width: 117px;
                height: 51px;
                background: $primary;
                color: $white;
                border: 0;
                margin-left: -8px;
                &:hover{
                    @media (min-width: 900px) {
                        color: $black;
                        background: #FDEFE9;
                        border-radius: 10%;
                        transform: scale(1.1);
                        transition: all .5s ease-in-out;
                        box-shadow: 5px 0 5px 5px $white;
                    }
                }
                @media (max-width: 422px){
                    max-width: 80px;
                }
            }
        }
    }
}