
    .button{
     background: $primary;
     border: 0;
     color: $white;
     font-weight: 500;
     font-size: $s_18;
     padding-top: 15px;
     padding-bottom: 15px;
     padding-left: 30px;
     padding-right: 30px;
        &:hover{
            @media (min-width: 900px) {
                color: $black;
                background: #FDEFE9;
                border-radius: 10%;
                transform: scale(1.1);
                transition: all .5s ease-in-out;
                box-shadow: 5px 0 5px 5px $white;
            }
        }
    }
