.header{
    .header__wrapper{
        display: flex;
        @media (max-width: 800px) {
            width: 100%;
            justify-content: space-between;
        }
        .header_hamburger{
            display: none;
            position: fixed;
            top: 20px;
            right: 20px;
            z-index: 1000;
            span{
                @media (max-width: 800px) {
                    width: 100%;
                    height: 2px;
                    margin-bottom: 3px;
                    margin-top: 3px;
                    background: #222;
                    transition: all .3s ease;
                    z-index: 2000;
                }
            }

            @media (max-width: 800px) {
              width: 35px;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              border: 0;
              background: transparent;
              border-radius: 4px;
              overflow: hidden;
              order: 4;
              padding: 0;
          }
          &.open{
              display: none;
              right: 20px;
              top: 60px;
              z-index: 10000;
              margin-top: -5px;
              @media (max-width: 800px) {
                  top: 30px;
                  display: flex;
              }
              span{
                  @media (max-width: 800px) {
                      background: $black;
                  }
                  &:first-child{
                      @media (max-width: 800px) {
                          transform: rotate(36deg) translate3D(3px, -3px, 0);
                          transform-origin: 0 0;
                      }
                  }

                  &:nth-child(2) {
                      @media (max-width: 800px) {
                          transform: translateX(-140%);
                          transform-origin: 0 0;
                      }
                  }

                  &:last-child {
                      @media (max-width: 800px) {
                          transform: rotate(-36deg) translate3D(0px, 5px, 0);
                          transform-origin: 0 100%;
                      }
                  }
              }
          }
      }
        .menu__nav{
            .header__button{
                    .button{
                    @media (max-width: 800px) {
                        margin: 0;
                    }
                }
            }
            .menu{
                @media (max-width: 800px) {
                    flex-direction: column;
                    justify-content: flex-start;
                    text-align: center;
                }
            }
            @media (max-width: 800px) {
                display: flex;
                width: 78%;
                position: fixed;
                z-index: 1000;
                top: -125%;
                left: 0;
                display: flex;
                height: 550px;
                background: #fadcd2;
                margin-top: 0;
                flex-direction: column;
                box-shadow: -10px 0px 15px 5px $black;
                padding-top: 16px;
                transition: all .8s ease;
            }
            &.open{
                @media (max-width: 800px) {
                    top: 0;
                    padding-left: 0;
                    margin-left: 0;
                    padding-top: 80px;
                }
                .item{
                    @media (max-width: 800px) {
                        padding-bottom: 30px;
                        padding-left: 0;
                        padding-right: 0;
                        .header__button{
                            @media (max-width: 800px) {
                                color: #222 !important;
                                font-size: $s_36;
                                margin-top: 1.176vw;
                            }
                        }
                    }
                    a{
                        @media (max-width: 800px) {
                            padding-left: 0;
                            color: $black !important;
                            font-size: $s_36;
                        }
                    }
                }
            }
        }
    }
}
