.background{
  background: url(../img/background.png) no-repeat left top;
}


.main__sub{
  .main__sub__wrapper{
      display: flex;
      justify-content: space-between;
      @media (max-width: 629px){
          flex-wrap: wrap;
          justify-content: center;
      }
      .sneakers-carousel{
          width: 50%;
          position: relative;
          padding-right: 20px;
          margin: 10px;
          @media (max-width: 1170px){
              width: 60%;
          }
          @media(max-width: 628px){
              width: 100%;
              padding-right: 0;
          }
          .owl-prev{
              background: url(../img/vector_carousel_02.svg) no-repeat center / cover;
              width: 100%;
              max-width: 70px;
              height: 70px;
              position: absolute;
              top: 140px;
              left: 0;
              &:hover{
                  @media (min-width: 900px){
                      transform: scale(1.1);
                      transition: all .4s ease-in-out;
                  }
              }
              &.disabled{
                  opacity: 0;
              }
              @media (max-width: 870px){
                  top: 120px;

              }
              @media (max-width: 775px){
                  top: 100px;
                  left: -7px;
              }
          }
          .owl-next{
              background: url(../img/vector_carousel.svg) no-repeat center / cover;
              width: 100%;
              max-width: 70px;
              height: 70px;
              position: absolute;
              top: 140px;
              right: 22px;
              &:hover{
                  @media (min-width: 900px){
                      transform: scale(1.1);
                      transition: all .4s ease-in-out;
                  }
              }
              &.disabled{
                  opacity: 0;
              }
              @media (max-width: 870px){
                  top: 120px;
              }
              @media (max-width: 775px){
                  top: 100px;
                  right: 0;
              }
          }
          .main__sub__img{
              img{
                  width: 100%;
                  max-width: 555px;
                  @media(max-width: 628px){
                      width: 99.667vw;
                  }
              }
          }
      }
      .main__sub__box{
          padding-right: 11.462vw;
          @media (max-width: 1170px){
              padding-right: 0;
          }
          h1{
              font-family: 'Source Serif Pro';
              font-style: normal;
              font-weight: 600;
              font-size: $s_61;
              line-height: 120%;
              padding-top: 4.615vw;
              display: block;
              span{
                  color: $primary;
              }
              @media  (max-width: 940px) {
                  font-size: 2.6rem;
              }
          }
          p{
              color: $black;
              opacity: 0.7;
              padding-top: 1.538vw;
              padding-bottom: 5vw;
              color: $s_16;
              @media  (max-width: 628px) {
                  padding-bottom: 4vw;
              }
          }
      }
  }
}
