.function{
    .function__wrapper{
        .function__insider{
            display: flex;
            @media (max-width: 820px){
               justify-content: center;
               flex-wrap: wrap;
            }
            .function__default{
                background: $white;
                box-shadow: 0px 1px 30px rgba(26, 10, 3, 0.05);
                border-radius: 10px;
                width: 100%;
                max-width: 360px;
                height: 423px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                margin-right: 2.083vw;
                text-align: center;
                @media (max-width: 820px){
                    margin-top: 20px;
                    max-width: 300px;
                    height: 335px;
                }
                &:last-child{
                    margin-right: 0;
                    @media (max-width: 820px){
                        margin-top: 20px;
                    }
                }
                &:nth-child(2){
                    @media (max-width: 820px){
                        margin-top: 20px;
                    }
                }
                &:hover{
                    @media (min-width: 900px){
                        background: #FDEFE9;
                        transition: all .4s ease-in-out;
                    }
                }
                img{
                    width: 100%;
                    max-width: 140px;
                }
                span{
                    font-family: 'Source Serif Pro';
                    font-weight: 600;
                    font-size: $s_22;
                    padding-top: 38px;
                    padding-bottom: 10px;
                    color: $black;
                }
                p{
                    font-weight: 400;
                    font-size: $s_14;
                    line-height: 160%;
                    color: $black;
                    opacity: 0.7;
                }
                button{
                    background: 0;
                    border: 0;
                    font-weight: 500;
                    font-size: $s_16;
                    color: $primary;
                    padding-top: 2.389vw;
                    &:hover{
                        @media (min-width: 900px){
                            transform: scale(1.1);
                            transition: all .5s ease-in-out;
                        }
                    }
                }
            }
        }
        .function__box{
            padding-top: 8.722vw;
            text-align: center;
            span{
                color: $primary;
                font-family: 'Source Serif Pro';
                font-weight: 600;
                font-size: $s_20;
            }
            h2{
                font-family: 'Source Serif Pro';
                font-weight: 600;
                font-size: $s_44;
                padding-top: 0.694vw;
                padding-bottom: 4.167vw;
            }
        }
    }
}