.tabs{
    .tabs-triggers{
        display: flex;
        padding-bottom: 4.236vw;
        padding-top: 8px;
        p{
            font-weight: 400;
            font-size: 20px;
            color: #1A0A03;
            opacity: 0.7;
            padding-right: 2.059vw;
            padding-bottom: 0.235vw;
        }
        a{
            font-weight: 400;
            font-size: $s_18;
            color: #1A0A03;
            opacity: 0.7;
            margin-right: 2.353vw;
            &.tabs-triggers__item--active{
                color: $primary;
                border-bottom: 1px solid $primary ;
            }
        }
    }
    .tabs-content{
        display: flex;
        .tabs-content__item{
            padding-right: 1.765vw;
            display: none;
            @media (max-width: 900px){
                padding-right: 0;
            }
            &.tabs-content__item--active{
                display: block;
            }
            img{
                width: 100%;
                max-width: 263px;
                margin-bottom: 2.083vw;
                background: url(image.png);
                border: 1px solid rgba(26, 10, 3, 0.1);
                filter: drop-shadow(0px 1px 30px rgba(26, 10, 3, 0.05));
                border-radius: 10px;
                &:hover{
                    @media (min-width: 900px){
                        border: 1px solid $primary;
                        transition: all .4s ease-in-out;
                        transform: scale(1.1);
                    }
                }
            }
            h3{
                font-family: 'Source Serif Pro';
                font-weight: 600;
                font-size: $s_22;
                color: $black;
            }
            p{
                font-weight: 500;
                font-size: $s_16;
                color: $black;
                opacity: 0.8;
                padding-top: 0.694vw;
                padding-bottom: 0.694vw;
            }
            span{
                color: $black;
                opacity: 0.7;
                font-weight: 400;
                font-size: $s_16;
                padding-right: 0.694vw;
            }
        }
        .tabs__wrapper{
            display: flex;
            @media (max-width: 800px){
                flex-wrap: wrap;
                justify-content: center;
            }
            .tabs__wrapper__item{
                margin-right: 20px;
                margin-top: 20px;
            }
        }
    }
}   