.testimonials{
    .testimonials__wrapper{
        .testimonials__insider{
            text-align: center;
            padding-top: 8.722vw;
            span{
                font-weight: 600;
                font-size: $s_44;
                color: $black;
            }
        }
        .testimonials__box{
            display: flex;
            @media (max-width: 800px){
                flex-wrap: wrap;
                flex-direction: column-reverse;
                align-items: center;;
            }
            img{
                width: 100%;
                max-width: 555px;
                padding-top: 3.529vw;
                @media (max-width: 980px){
                    max-width: 400px;
                }
                @media (max-width: 800px){
                    max-width: 480px;
                }
            }
            .testimonials__wrap{
                padding-left: 7.471vw;
                flex-direction: column;
                display: flex;
                @media (max-width: 980px){
                    padding-left: 4.471vw;
                }
                .testimonials__logo{
                    width: 100%;
                    max-width: 100px;
                }
                .testimonials__images{
                    width: 100%;
                    max-width: 36px;
                    padding-bottom: 1.765vw;
                }
                h3{
                    font-family: 'Source Serif Pro';
                    font-weight: 600;
                    font-size: 22px;
                }
                .testimonials__text{
                    font-style: italic;
                    font-weight: 500;
                    font-size: 14px;
                    color: #1A0A03;
                    opacity: 0.7;
                    padding-top: 0.882vw;
                    padding-bottom: 1.471vw;
                }
                .testimonials__name{
                    font-weight: 500;
                    font-size: 18px;
                    color: #1A0A03;
                    padding-bottom: 0.588vw;
                }
                .testimonials__last{
                    color: #1A0A03;
                    opacity: 0.7;
                }
            }
        }
    }
}