.header{
    .header__wrapper{
        display: flex;
        justify-content: space-between;
        height: 140px;
        @media (max-width: 900px) {
            height: 90px;
        }
        .header__logo{
            display: flex;
            align-content: center;
            img{
                width: 100%;
                max-width: 94px;
                z-index: 3000;
                @media (min-width: 800px) {
                    &:hover{
                        transform: scale(1.2);
                        transition: all 0.5s ease-in-out;
                    }
                }
                @media (max-width: 800px) {
                  position: fixed;
                  top: 20px;
                  left: 20px;
                }
            }
        }
        .menu__nav{
            display: flex;
            align-items: center;
            .menu{
                display: flex;
                .item{
                    padding-right: 40px;
                    &:last-child{
                        margin-right: 22.692vw;
                        @media  (max-width: 900px) {
                           margin-right: 0px;
                        }
                    }
                    a{
                        color: #1A0A03;
                        font-weight: 500;
                        font-size: 16px;
                        @media  (min-width: 1000px) {
                            &:hover{
                                color: $primary;
                                transition: all .4s ease;
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
        }
    }
  }
