.video{
    .video__wrapper{
        display: flex;
        justify-content: space-between;
        @media (max-width: 636px){
            flex-wrap: wrap;
        }
        .video__insider{
            position: relative;
            margin-top: 11.923vw;
            display: flex;
                align-items: center;
                justify-content: center;
            .video__01{
                width: 100%;
                max-width: 650px;
                padding-right: 3.846vw;
                padding-left: 1.7vw;
                position: relative;
                @media (max-width: 1000px){
                    padding-right: 0;
                }
            }
            .video__02{
                position: absolute;
                img{
                    width: 100%;
                    max-width: 100px;
                    margin-right: 45px;
                    margin-top: 45px;
                    &:hover{
                        @media (min-width: 900px){
                            transform: scale(1.1);
                            transition: all .4s ease-in-out;
                        }
                    }
                    @media (max-width: 1000px){
                        max-width: 60px;
                    }
                }
            }
        }
        .video__box{
            padding-top: 13.692vw;
            span{
                font-family: 'Source Serif Pro';
                font-style: normal;
                font-weight: 600;
                font-size: $s_20;
                color: $primary;
                padding-bottom: 0.588vw;
            }
            h2{
                font-family: 'Source Serif Pro';
                font-style: normal;
                font-weight: 600;
                font-size: $s_44;
                line-height: 130%;
                padding-bottom: 1.176vw;
                @media (max-width: 1000px){
                    padding-right: 1.176vw;
                    br{
                        display: none;
                    }
                }
                @media (max-width: 636px){
                    br{
                        display: block;
                    }
                }
            }
            p{
                color: $black;
                opacity: 0.7;
                font-size: $s_16;
            }
        }
    }
}